@import "../../../sass/consts";

.museum_introduce_page {
  .introduce_banner {
    height: 30vh !important;

    img {
      height: 100%;
    }
  }

  .introduce {
    border-radius: 1rem 1rem 0 0;
    background-color: white;
    position: fixed;
    top: 30vh;
    height: 70vh !important;
    width: 100%;
    z-index: 10;

    .infos {
      padding: 0 1rem;

      .name {
        font-weight: 700;
        font-size: 1.2rem;
        line-height: 4rem;
      }

      .info {
        color: #666;
        font-size: .8rem;

        &.phone {
          color: $bg4;
          padding-bottom: 1rem;
        }

        img {
          width: 1rem;
        }

        span {
          padding-left: .5rem;


        }
      }
    }

    .synopsis {
      font-size: .8rem;
      line-height: 1.5rem;
      padding: 0 1rem;
      overflow-y: scroll;
    }
  }
}
